import global_ from '../../common/js/global.js'
export default {
	data() {
		return {
			list: {},
			imgData: [],
			url: global_.baseUrl,
			head: ''
		}
	},
	onLoad(option) {
		this.list = JSON.parse(option.list)
		this.imgData = this.list.material.split('|')
		this.head = this.$store.state.head
	}
}
